:root {
  --font-primary: "Outfit", sans-serif;
}

:root {
  --body-background: hsl(212, 45%, 89%);
  --card-background: hsl(0, 0%, 100%);
  --title-color: hsl(218, 44%, 22%);
  --paragraph-color: hsl(220, 15%, 55%);
}

html {
  font-size: 100%;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
  display: block;
}

body {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  background-color: var(--body-background);
  font-family: var(--font-primary);
  padding: 1.875rem;
}
@media (min-width: 48em) {
  body {
    padding: 0;
  }
}

.card {
  background-color: var(--card-background);
  max-width: 20rem;
  padding: 1rem;
  border-radius: 1rem;
}
.card__qrcode {
  border-radius: 0.625rem;
}
.card__content {
  padding: 1.5rem 0.25rem;
}
@media (max-width: 21.875em) {
  .card__content {
    padding: 1.5rem 0;
  }
}
.card__title {
  font-size: 1.375rem;
  text-align: center;
  color: var(--title-color);
  margin-bottom: 1rem;
}
@media (max-width: 21.875em) {
  .card__title {
    font-size: 1rem;
  }
}
.card__description {
  font-size: 0.9375rem;
  text-align: center;
  color: var(--paragraph-color);
}
@media (max-width: 21.875em) {
  .card__description {
    font-size: 0.875rem;
  }
}